import { RegCarData } from '../../../../definitions/car-data.def';
import { BaseComp } from '../../../../lib/components/base-comp/base-comp';

export class GaragePopupWinner extends BaseComp {
  private header = document.createElement('h3');

  private textTime = document.createElement('span');

  private textName = document.createElement('span');

  private textId = document.createElement('span');

  private textNumber = document.createElement('span');

  constructor() {
    super('div');
    this.header.textContent = 'WE HAVE A WINNER!';
    this._rootElement.append(
      this.header,
      this.textTime,
      this.textName,
      this.textId,
      this.textNumber,
    );
  }

  setData(car: RegCarData, timeStr: string, indexStr?: string) {
    this.textTime.textContent = `Time: ${timeStr}s`;
    this.textName.textContent = `Name of the car: \n ${car.name}`;
    this.textId.textContent = `ID of the car: ${car.id}`;
    this.textNumber.textContent = indexStr
      ? `Position on the track: ${indexStr}`
      : ``;
  }
}
