import { DBusInterface } from '../../../../../lib/services/dbus/defs/dbus-interface.def';
import { DBusPort } from '../../../../../lib/services/dbus/defs/dbus-port.def';

export interface CreateUpdateCompControllerReqElems {
  inputText: HTMLInputElement;
  inputColor: HTMLInputElement;
  btn: HTMLButtonElement;
}

export abstract class CreateUpdateCompControllerProto implements DBusPort {
  constructor(
    protected inputText: HTMLInputElement,
    protected inputColor: HTMLInputElement,
    protected btn: HTMLButtonElement,
  ) {}

  abstract autoConnect(int: DBusInterface): void | Promise<void>;

  protected toogleCtlsDisabled(state: boolean) {
    this.inputText.disabled = state;
    this.inputColor.disabled = state;
    this.btn.disabled = state;
  }
}
