import { OBJ_PROCESSOR } from '../../processors/obj-processor';
import { DBUS_HUB_NAMES } from '../dbus/defs/dbus-hub-names.def';
import { DBusHub, DBusInterface } from '../dbus/defs/dbus-interface.def';
import {
  DBusTXPrepModes,
  DBusTXWaitModes,
} from '../dbus/defs/dbus-tx/dbus-tx-modes.def';
import { DBusTX } from '../dbus/defs/dbus-tx/dbus-tx.def';
import { DBusMsgRouteIsChangedMsg } from './dbus-msgs-defs/types/dbus-msg-route-is-chaned-msg';
import { SimpleRouterAgent } from './simple-router-agent';

export interface RouterOptions {
  mode: string;
  onlyDBus?: true;
  defaultRoot?: string;
  defaultAgent?: SimpleRouterAgent;
}

export class SimpleRouter implements DBusHub {
  readonly hubName = DBUS_HUB_NAMES.ROUTER;

  private agents: Set<SimpleRouterAgent> = new Set();

  private options: RouterOptions;

  private currRoute: string | undefined;

  private txs: Set<DBusTX> = new Set();

  private defaultAgent: SimpleRouterAgent | undefined;

  private restrictions: Set<() => Promise<boolean>> = new Set();

  constructor(options: RouterOptions) {
    switch (options.mode) {
      case '#':
        // just filter that # mode
        break;
      case '?':
        throw new Error(`${options.mode} is not supported yet!`);
      default:
        throw new Error(`${options.mode} is not supported!`);
    }
    this.options = OBJ_PROCESSOR.deepFreeze(options);

    window.addEventListener('popstate', async () => {
      if (window.location.href === this.currRoute) return;

      if (this.restrictions.size > 0) {
        const wantedRoute = window.location.href;

        if (this.currRoute) {
          window.location.href = this.currRoute;
        } else if (this.options.defaultRoot) {
          window.location.href = `${window.location.host}/${this.options.defaultRoot}`;
        }
        const arr = [...this.restrictions.values()];
        const promises = arr.map(arrow => arrow());
        const result = await Promise.all(promises);
        let cancelRouteChange = false;

        if (Array.isArray(result)) {
          for (let i = 0; i < result.length; i++) {
            if (typeof result[i] !== 'boolean')
              throw new Error('restrict func returned not boolean value!');

            if (!result[i]) cancelRouteChange = true;
            else {
              const func = arr[i];

              if (func) this.restrictions.delete(func);
            }
          }
        }

        if (!cancelRouteChange) window.location.href = wantedRoute;
      } else {
        this.activateWantedAgents();
      }
    });
  }

  activateWantedAgents(): boolean | Error[] {
    this.currRoute = window.location.href;
    let activatedCount = 0;
    const errors = [];

    if (!this.options.onlyDBus && this.options.mode === '#') {
      const agentsArr = [...this.agents];
      for (let i = 0; i < this.agents.size; i++) {
        if (agentsArr[i]?.path === window.location.hash) {
          try {
            agentsArr[i]?.callback();
          } catch (error) {
            error.push(error);
          }
          activatedCount++;
          break;
        }
      }

      if (activatedCount === 0) {
        if (
          this.defaultAgent &&
          this.options.defaultRoot === window.location.hash
        ) {
          this.defaultAgent.callback();
        } else {
          errors.push(new Error('Agent is not finded'));
        }
      }
    }

    if (this.txs.size > 0) {
      this.txs.forEach(tx => {
        const msg = new DBusMsgRouteIsChangedMsg(window.location.hash);
        tx.transmit({
          msg,
          waitMode: DBusTXWaitModes.nowait,
          prepMode: DBusTXPrepModes.freeze,
        });
      });
    }

    if (errors.length > 0) return errors;
    return activatedCount > 0;
  }

  addRouterAgent(agent: SimpleRouterAgent): void {
    this.agents.add(agent);
  }

  removeRouterAgent(agent: SimpleRouterAgent): void {
    this.agents.delete(agent);
  }

  flushRouterAgents(): void {
    this.agents.clear();
  }

  /* not refactored yet
  private watchRestrictions = async (msg: DataBusRoutingMsg) => {
    if (msg instanceof DataBusMsgRoutingRoutingRestrictMode) {
      const func = msg.data.callbackPromiseGenerator as () => Promise<boolean>;

      if (msg.data.mode) {
        this.restrictions.add(func);
      } else {
        this.restrictions.delete(func);
      }
    }
  };
  */

  connect(int: DBusInterface): void {
    const tx = int.getNewTX('broadcasting route changes');
    this.txs.add(tx);
    // watch restriction
    // not refactored yet
    // const rx = bus.getNewRX();
    // bus.registerReceiver(DataBusMsgTopics.ROUTING, msgReceiver);
  }
}
