import { BaseComp } from '../../../../lib/components/base-comp/base-comp';

export interface NavItemStyles {
  li?: string;
  a?: string;
  svg?: string;
  span?: string;
}

export class NavItem extends BaseComp {
  private _anchor: HTMLAnchorElement;

  protected iconImage: HTMLImageElement | undefined;

  protected span: HTMLSpanElement;

  constructor(
    text: string,
    href: string,
    styles?: NavItemStyles,
    iconSrc?: string,
  ) {
    super('li');

    if (styles?.li) this._rootElement.classList.add(styles.li);
    this._anchor = document.createElement('a');
    this._anchor.setAttribute('href', href);

    if (styles?.a) this._anchor.classList.add(styles.a);

    if (iconSrc) {
      this.iconImage = document.createElement('img');
      this.iconImage.src = iconSrc;

      if (styles?.svg) this.iconImage.classList.add(styles.svg);
      this._anchor.append(this.iconImage);
    }
    this.span = document.createElement('span');
    this.span.textContent = text;

    if (styles?.span) this.span.classList.add(styles.span);
    this._anchor.append(this.span);

    this.rootElement.append(this._anchor);
  }

  get anchor(): HTMLAnchorElement {
    return this._anchor;
  }
}
