import { BaseComp } from '../../lib/components/base-comp/base-comp';
import { DBUS_HUB_NAMES } from '../../lib/services/dbus/defs/dbus-hub-names.def';
import {
  DBusHub,
  DBusInterface,
} from '../../lib/services/dbus/defs/dbus-interface.def';
import { GarageComponentControllerAkaDirector } from './garage-controller';
import { MainControlsComp } from './subcomps/main-controls/main-controls';

import styles from './garage.scss';
import sharedStyles from '../../styles.scss';
import { Notifiable } from '../../lib/interfaces/notifiable';
import { PopupStandaloneModalProgressComp } from '../../lib/components/popups/standalone/modal/progress/popup-progress';

export class GarageComp extends BaseComp implements DBusHub, Notifiable {
  readonly hubName = DBUS_HUB_NAMES.GARAGE;

  private controller: GarageComponentControllerAkaDirector;

  private isControllerWorking = false;

  private mainControlsComp = new MainControlsComp();

  private carsCtlsBlock = document.createElement('div');

  private bgVideo = document.createElement('video');

  private popup = new PopupStandaloneModalProgressComp();

  constructor() {
    super('section');
    this.popup.setText('Loading...').setProgress(1).show();
    this._rootElement.classList.add(styles.garage);

    const tracksContainer = document.createElement('div');
    tracksContainer.classList.add(
      styles['garage__track-container'],
      styles['synthwave-background'],
    );

    const selfCtlsBlock = document.createElement('div');
    selfCtlsBlock.classList.add(styles['garage-bg__self-ctls-block']);
    selfCtlsBlock.append(this.mainControlsComp.rootElement);

    const bg = document.createElement('div');
    bg.classList.add(styles.garage__bg, styles['garage-bg']);

    this.bgVideo.classList.add(styles['garage-bg__video']);

    this.bgVideo.controls = false;
    this.bgVideo.loop = true;
    this.bgVideo.defaultMuted = true;
    this.bgVideo.muted = true;
    this.bgVideo.autoplay = true;
    this.bgVideo.addEventListener('progress', this.bgVideoProgressListener);
    this.bgVideo.addEventListener(
      'canplaythrough',
      this.bgVideoCanPlayListener,
    );
    this.bgVideo.src = './assets/videos/bg/cyberpunk2077-garage.mp4';

    bg.append(this.bgVideo, selfCtlsBlock);
    this.controller = new GarageComponentControllerAkaDirector(
      tracksContainer,
      this.carsCtlsBlock,
    );
    this.carsCtlsBlock.classList.add(styles['garage-bg__cars-ctls-block']);
    this._rootElement.append(bg, tracksContainer, this.carsCtlsBlock);
  }

  private bgVideoProgressListener = () => {
    if (this.bgVideo.buffered.length > 0) {
      const progress =
        (this.bgVideo.buffered.end(0) / this.bgVideo.duration) * 100;
      this.popup.setProgress(progress);
    }
  };

  private bgVideoCanPlayListener = async () => {
    this.bgVideo.removeEventListener(
      'canplaythrough',
      this.bgVideoCanPlayListener,
    );
    this.bgVideo.removeEventListener('progress', this.bgVideoProgressListener);
    this.isControllerWorking = await this.controller.startWorking();
    this.popup.setProgress(100).reset().hide();
  };

  connect(int: DBusInterface) {
    int.attachSubHub(this.controller);
    int.attachSubHub(this.mainControlsComp);
  }

  notify = async () => {
    try {
      this.bgVideo.play();
    } catch {
      // do nothing...
    }
    if (!this.isControllerWorking) {
      this.popup.setProgress(1).setText('Loading...').show();
      this.isControllerWorking = await this.controller.startWorking();
      this.popup.reset().hide();
    }
  };
}
