import { UnregCarData } from '../../definitions/car-data.def';
import { DBUS_COMMON_RESPONSES } from '../../lib/services/dbus/defs/dbus-common-responses.def';
import {
  DBusHubNamesList,
  DBUS_HUB_NAMES,
} from '../../lib/services/dbus/defs/dbus-hub-names.def';
import {
  DBusHub,
  DBusInterface,
} from '../../lib/services/dbus/defs/dbus-interface.def';
import { DBusPort } from '../../lib/services/dbus/defs/dbus-port.def';
import { DBusTopics } from '../../lib/services/dbus/defs/dbus-topics.def';
import { DBusTXData } from '../../lib/services/dbus/defs/dbus-tx/dbus-tx-data.def';
import { DBusTX } from '../../lib/services/dbus/defs/dbus-tx/dbus-tx.def';
import { REST_CLIENT_GARAGE } from '../../services/rest-clients/client-garage/rest-client-garage';
import { DBusMsgCarActionRemovedMsg } from './dbus-msgs/by-topic/car-action/garage-car-remove-msg.def';
import { GarageManagersTeam } from './garage-servant';
import { CarComp } from './subcomps/car/car';
import { TrackComp } from './subcomps/track/track';
import { GarageServicemanContract } from './garage-serviceman-contract.def';
import { DBusMsgCarActionCreatedMsg } from './dbus-msgs/by-topic/car-action/garage-car-created-msg.def';
import { DBusMsgRaceRequestMsg } from './dbus-msgs/by-topic/garage/garage-race-request-msg.def';
import { DBusMsgGarageResetReqMsg } from './dbus-msgs/by-topic/garage/garage-reset-req-msg.def';

import styles from './garage.scss';
import sharedStyles from '../../styles.scss';
import { Pager } from '../../lib/services/pager/pager';
import { Notifiable } from '../../lib/interfaces/notifiable';
import { DBusMsgGenerateCarsMsg } from './subcomps/main-controls/dbus-msgs/generate-cars-msg.def';

type TracksGeometry = {
  trackHeights: number[];
  displacements: number[];
  paddingBase: number;
  paddingAddShift: number;
};

export class GarageComponentControllerAkaDirector implements DBusHub {
  static readonly DISPLACEMENT_FIX = -20;

  readonly hubName = DBUS_HUB_NAMES.GARAGE_CONTROLLER;

  protected txs = new Set<DBusTX>();

  private garageManagersTeam: GarageManagersTeam;

  private pager: Pager;

  private entriesPerPage = 7;

  private pagerContainer = document.createElement('div');

  private readonly carImgRatio = 3.78;

  private readonly scaleFactor = 0.95;

  constructor(
    private tracksContainer: HTMLElement,
    private carCtlsBlock: HTMLElement,
  ) {
    this.garageManagersTeam = new GarageManagersTeam(
      tracksContainer,
      carCtlsBlock,
    );

    this.pagerContainer.classList.add(styles['garage-bg__pager']);
    this.pager = new Pager(this.entriesPerPage, this.pagerCallback);

    const btnNextPage = this.pager.getBtnNextPage();
    const btnPrevPage = this.pager.getBtnPrevPage();
    btnNextPage.classList.add(
      sharedStyles.btn,
      sharedStyles['btn-6'],
      styles['garage-bg__pager-btn'],
    );
    btnPrevPage.classList.add(
      sharedStyles.btn,
      sharedStyles['btn-6'],
      styles['garage-bg__pager-btn'],
    );

    const pagerInfo = this.pager.getPagerSpan();
    pagerInfo.classList.add(styles['garage-bg__pager-span']);

    this.pagerContainer.append(btnPrevPage, btnNextPage, pagerInfo);
  }

  private pagerCallback = async (dir: 'prev' | 'next'): Promise<boolean> => {
    try {
      await this.reloadData(dir);
      return true;
    } catch {
      return false;
    }
  };

  async startWorking(): Promise<boolean> {
    try {
      await this.reloadData('no');
      window.addEventListener('resize', () => this.redraw());
      return true;
    } catch {
      return false;
    }
  }

  async connect(int: DBusInterface) {
    this.txs.add(int.getNewTX('send requests to REST Garage client'));
    this.garageManagersTeam.autoConnect(int);

    const carActionsMsgsRX = int.getNewRX(
      this.carActionsRXFunc,
      'monitor cars removes',
    );
    int.plugRX(DBusTopics.CAR_ACTION, carActionsMsgsRX);

    const raceMsgsRX = int.getNewRX(this.racingRXFunc, 'handle race requsets');
    int.plugRX(DBusTopics.GARAGE, raceMsgsRX);

    const generatorMsgsRX = int.getNewRX(this.generatorRXFunc);
    int.plugRX(DBusTopics.GENERATE, generatorMsgsRX);
  }

  private generatorRXFunc = async (data: DBusTXData) => {
    if (
      data.msg instanceof DBusMsgGenerateCarsMsg &&
      data.msg.status === 'end'
    ) {
      const contracts = this.garageManagersTeam.getContracts();

      if (contracts.size < 7) this.reloadData('no');
    }
    return DBUS_COMMON_RESPONSES.VOID;
  };

  private racingRXFunc = async (data: DBusTXData) => {
    if (data.msg instanceof DBusMsgRaceRequestMsg) {
      this.pager.toogleLock(true);
      await this.performRace();
      this.pager.toogleLock(false);
      return true;
    }
    if (data.msg instanceof DBusMsgGarageResetReqMsg) {
      return this.resetState();
    }
    return DBUS_COMMON_RESPONSES.VOID;
  };

  private async resetState() {
    const reseted = await this.garageManagersTeam.resetState();
    return reseted;
  }

  private async performRace() {
    await this.garageManagersTeam.performRace();
  }

  private carActionsRXFunc = async (data: DBusTXData) => {
    if (
      data.msg instanceof DBusMsgCarActionRemovedMsg ||
      data.msg instanceof DBusMsgCarActionCreatedMsg
    ) {
      try {
        this.reloadData('no');
      } catch {
        this.pager.tryChangePage('prev');
      }
    }

    return DBUS_COMMON_RESPONSES.VOID;
  };

  private reloadData(dir: 'prev' | 'next' | 'no') {
    return new Promise<boolean>((res, rej) => {
      const page = dir
        ? dir === 'next'
          ? this.pager.getCurrPageNumber() + 1
          : dir === 'prev'
          ? this.pager.getCurrPageNumber() - 1
          : dir === 'no'
          ? this.pager.getCurrPageNumber()
          : 1
        : 1;
      REST_CLIENT_GARAGE.getCars(page, this.pager.getEntriesPerPage()).then(
        async response => {
          const { limit, cars } = response;
          this.pager.setEntriesCount(Number.parseInt(limit, 10));
          if (cars.length === 0 && dir !== 'no') {
            rej();
            return;
          }
          await this.garageManagersTeam.nullifyContracts();
          const newContracts: GarageServicemanContract[] = [];

          cars.forEach((car, index) => {
            const track = new TrackComp();
            const carComp = new CarComp(car, index + 1);
            track.placeCar(carComp);
            newContracts.push({ track, car: carComp });
          });

          this.garageManagersTeam.addContracts(...newContracts);
          this.garageManagersTeam.resetState();
          this.redraw();
          res(true);
        },
      );
    });
  }

  private redraw() {
    this.clearDOM();
    const tracks = [...this.garageManagersTeam.getContracts()].map(
      contract => contract.track,
    );

    const geometry = this.calcGeometry();
    this.setStylesForTracks(geometry);

    this.drawTracks();
    this.drawCarsCtls();
  }

  private clearDOM() {
    while (this.tracksContainer.firstChild)
      this.tracksContainer.firstChild.remove();
    while (this.carCtlsBlock.firstChild) this.carCtlsBlock.firstChild.remove();
  }

  private calcGeometry(): TracksGeometry {
    const style = window.getComputedStyle(this.tracksContainer);
    const containerHeight =
      Number.parseFloat(style.height) -
      Number.parseFloat(style.paddingBottom) -
      Number.parseFloat(style.paddingTop);
    const firstAutoWidth = window.innerWidth * 0.2;
    const firstAutoHeight = firstAutoWidth / this.carImgRatio;
    const heights: number[] = [];
    let heightSum = 0;
    const contracts = [...this.garageManagersTeam.getContracts()];

    contracts.forEach((contract, index) => {
      const factor = this.scaleFactor ** index;
      const carTrackHeight = firstAutoHeight * factor;
      heightSum += carTrackHeight;
      heights.push(carTrackHeight);
    });

    const excessHeight = heightSum - containerHeight;
    const heightsReversed = heights.sort((a, b) => a - b);
    const displacementBase =
      excessHeight > 0
        ? excessHeight / this.garageManagersTeam.getContracts().size - 2
        : 0;

    const displacements: number[] = [];
    const midIndex = (contracts.length - 1) / 2;

    contracts.forEach((contract, index) => {
      const diff = index - midIndex;
      const displacement =
        displacementBase * -diff +
        GarageComponentControllerAkaDirector.DISPLACEMENT_FIX;
      displacements.push(displacement);
    });

    const paddingBase = (heightsReversed[0] as number) * this.carImgRatio;
    const paddingAddShift = (20 * window.innerWidth) / 1920; // 20 - is good looking on 1920

    return {
      trackHeights: heightsReversed,
      displacements,
      paddingBase,
      paddingAddShift,
    };
  }

  private setStylesForTracks(geometry: TracksGeometry) {
    const contracts = [...this.garageManagersTeam.getContracts()];

    contracts.forEach((contract, index) => {
      contract.track.rootElement.style.setProperty(
        'height',
        `${geometry.trackHeights[index]}px`,
      );

      // if (index !== 0) {
      contract.track.rootElement.style.setProperty(
        'top',
        `${geometry.displacements[index]}px`,
      );
      // }

      contract.track.rootElement.style.setProperty('z-index', `${index + 1}`);

      contract.track.rootElement.style.setProperty(
        '--padding-w',
        `${
          (geometry.trackHeights[
            geometry.trackHeights.length - 1 - index
          ] as number) *
            this.carImgRatio -
          geometry.paddingBase +
          geometry.paddingAddShift * (contracts.length - 1 - index)
        }px`,
      );
    });
  }

  private drawTracks() {
    this.tracksContainer.append(
      this.pagerContainer,
      ...[...this.garageManagersTeam.getContracts()].map(
        contract => contract.track.rootElement,
      ),
    );
  }

  private drawCarsCtls() {
    const contracts = [...this.garageManagersTeam.getContracts()];
    const ctlBlocks = contracts.map((contract, index) => {
      const block = this.createCarCtlBlock(contract.car, index + 1);
      this.connectCtlAndTrack(block, contract.track);
      return block;
    });
    this.carCtlsBlock.append(...ctlBlocks);
  }

  private createCarCtlBlock = (car: CarComp, index: number): HTMLElement => {
    const btns = [car.btnSelect, car.btnStart, car.btnReset, car.btnRm];

    car.btnStart.classList.add(sharedStyles['btn-15']);
    car.btnSelect.classList.add(sharedStyles['btn-13']);
    car.btnReset.classList.add(sharedStyles['btn-9']);
    car.btnRm.classList.add(sharedStyles['btn-4']);

    btns.forEach(btn => {
      btn.classList.add(sharedStyles.btn, styles['garage-bg__car-ctl-btn']);
    });

    const numberSpan = document.createElement('span');
    numberSpan.classList.add(styles['garage-bg__car-ctl-block-number']);
    numberSpan.textContent = index.toString();
    const container = document.createElement('div');
    container.classList.add(styles['garage-bg__car-ctl-block']);
    container.append(numberSpan, ...btns);
    return container;
  };

  private connectCtlAndTrack = (ctlElem: HTMLElement, track: TrackComp) => {
    ctlElem.addEventListener(
      'mouseover',
      () => track.illuminateSelf(true),
      true,
    );
    ctlElem.addEventListener(
      'mouseout',
      () => track.illuminateSelf(false),
      true,
    );
  };
}
