import { RegCarData, UnregCarData } from '../../../../definitions/car-data.def';
import { BaseComp } from '../../../../lib/components/base-comp/base-comp';
import { hexToRgb } from '../../../../lib/convertors/colors';
import { transitionPromise } from '../../../../lib/gui/animation';
import { OBJ_PROCESSOR } from '../../../../lib/processors/obj-processor';
import styles from './car.scss';
import garageStyles from '../../garage.scss';

export class CarComp extends BaseComp {
  static readonly TIMING_FUNCTION = 'cubic-bezier(.72,.2,.54,.77)';

  static readonly CAR_BODY_IMG_SRC = './assets/imgs/cars/body.gif';

  static readonly CAR_WHEEL_IMG_SRC = './assets/imgs/cars/tire back.gif';

  static readonly CAR_EXPLOSION_IMG_DATA = {
    BASE_URL: './assets/imgs/cars/explosion/',
    EXTENSION: '.png',
    FRAMES_COUNT: 9,
    FRAME_TIME_MS: 200,
  };

  protected canvas = document.createElement('canvas');

  protected tireBack = document.createElement('img');

  private btnSelect$ = document.createElement('button');

  private btnRm$ = document.createElement('button');

  private _spanCarName = document.createElement('span');

  private btnStart$ = document.createElement('button');

  private btnReset$ = document.createElement('button');

  private startDrivingTime: number | undefined;

  private destinationCoordinate: number | undefined;

  private currAnimation: Animation | undefined;

  private currOutOfSceneAnim: Animation | undefined;

  private isReseted = true;

  private observer: MutationObserver;

  private movingClassName = garageStyles['synthwave-background_race-mode'];

  constructor(private _currCar: RegCarData, public index?: number) {
    super('div');

    this._rootElement.classList.add(styles['car-div']);
    this.canvas.classList.add(styles['car-div__canvas']);
    this._rootElement.append(this.canvas);

    const selectSpan = document.createElement('span');
    const rmSpan = document.createElement('span');
    const startSpan = document.createElement('span');
    const resetSpan = document.createElement('span');

    selectSpan.textContent = 'select';
    rmSpan.textContent = 'remove';
    startSpan.textContent = 'start!';
    resetSpan.textContent = 'reset';

    this.btnSelect$.append(selectSpan);
    this.btnRm$.append(rmSpan);
    this.btnReset$.append(resetSpan);
    this.btnStart$.append(startSpan);

    this.btnReset$.disabled = true;

    CarComp.drawCar(this.canvas, this._currCar.color);
    this._spanCarName.textContent = `${index}: ${_currCar.name}`;
    CarComp.drawWheels(this._rootElement);

    this.observer = new MutationObserver(mutations => {
      mutations.forEach(mutation => {
        if (mutation.oldValue?.includes(this.movingClassName)) {
          this.currOutOfSceneAnim?.pause();
          this.observer.disconnect();
        } else {
          const elem = mutation.target as HTMLElement;
          if (elem.classList.contains(this.movingClassName)) {
            this.outOfScene();
          }
        }
      });
    });
  }

  static drawWheels(rootElem: HTMLElement) {
    const wheelImg = new Image();
    wheelImg.src = CarComp.CAR_WHEEL_IMG_SRC;
    wheelImg.onload = () => {
      wheelImg.classList.add(styles['car-div__wheel']);
      const backwardWheelImg = OBJ_PROCESSOR.deepClone(wheelImg);
      wheelImg.classList.add(styles['car-div__wheel_forward']);
      backwardWheelImg.classList.add(styles['car-div__wheel_backward']);

      rootElem.append(wheelImg);
      rootElem.append(backwardWheelImg);
    };
  }

  updateData(carData: RegCarData) {
    this._currCar = carData;
    CarComp.drawCar(this.canvas, this._currCar.color);
    this._spanCarName.textContent = `${this.index}: ${carData.name}`;
    // this._rootElement.style.setProperty('background-color', carData.color);
  }

  public async rollBack(finishPromise: Promise<unknown> = Promise.resolve()) {
    return new Promise(res => {
      const asyncFunc = async () => {
        if (!this.isReseted) {
          this.observer.disconnect();
          await this.playOutAnim();
          if (this.currOutOfSceneAnim) {
            this.currOutOfSceneAnim.cancel();
            // await this.currOutOfSceneAnim.finished;
            this.clearAnimFromElem(this.currOutOfSceneAnim);
            this.currOutOfSceneAnim = undefined;
          }
          if (this.currAnimation) {
            await this.stop();
            this.currAnimation.playbackRate = -10;
            this.currAnimation.play();
            console.log(`wait curr anim ${this.index}`);
            await this.currAnimation.finished;
            this.clearAnimFromElem(this.currAnimation);
            this.currAnimation = undefined;
            this._rootElement.classList.remove(styles['car-div_rollForward']);
          }
          this._rootElement.style.removeProperty('transition');
          this._rootElement.style.removeProperty('transform');
          await this.playInAnim();
          this._rootElement.style.removeProperty('opacity');
          finishPromise.finally(() => {
            this.btnStart$.disabled = false;
            this.btnReset$.disabled = true;
            this.btnRm$.disabled = false;
            this.btnSelect$.disabled = false;
          });
        }
        console.log(`reseted ${this.index}`);
        this.isReseted = true;
        res(true);
      };
      asyncFunc();
    });
  }

  async playOutAnim() {
    const animOut = this._rootElement.animate(
      [
        { opacity: `0.5` },
        { opacity: `0.9` },
        { opacity: `0.3` },
        { opacity: `0.7` },
        { opacity: `0.1` },
        { opacity: `0.4` },
        { opacity: `0` },
      ],
      {
        duration: 300,
        easing: 'linear',
        fill: 'forwards',
      },
    );
    await animOut.finished;
    this.clearAnimFromElem(animOut);
  }

  async playAwaitingAnim(promiseToAwait: Promise<unknown>) {
    const animIn = this._rootElement.animate(
      [
        { opacity: `0` },
        { opacity: `0.6` },
        { opacity: `0.3` },
        { opacity: `0.8` },
        { opacity: `0.3` },
        { opacity: `0.6` },
        { opacity: `0` },
      ],
      {
        duration: 400,
        easing: 'linear',
        fill: 'forwards',
        iterations: Number.POSITIVE_INFINITY,
      },
    );
    await promiseToAwait.finally(() => {
      animIn.cancel();
      this.clearAnimFromElem(animIn);
    });
  }

  async playInAnim() {
    const animIn = this._rootElement.animate(
      [
        { opacity: `0` },
        { opacity: `0.6` },
        { opacity: `0.3` },
        { opacity: `0.8` },
        { opacity: `0.5` },
        { opacity: `1` },
      ],
      {
        duration: 300,
        easing: 'linear',
        fill: 'forwards',
      },
    );
    await animIn.finished;
    this.clearAnimFromElem(animIn);
  }

  private clearAnimFromElem = (a: Animation) => {
    const anim = a as unknown as Record<string, unknown>;
    if ('commitStyles' in anim && typeof anim.commitStyles === 'function')
      anim.commitStyles();
  };

  private outOfScene() {
    // console.log(`OUT OF SCENE ${this.index}`);
    this.isReseted = false;
    this.currOutOfSceneAnim = this._rootElement.animate(
      [{ transform: `translateX(${-1000}%)` }],
      {
        duration: 3600,
        easing: 'ease-in',
        fill: 'forwards',
      },
    );
  }

  public disableCtls() {
    this.btnStart$.disabled = true;
    this.btnRm$.disabled = true;
    this.btnSelect$.disabled = true;
    this.btnReset$.disabled = true;
  }

  public async rollForward(time: number, handleBtns = true) {
    this.isReseted = false;
    // console.log(`anim time: ${time} ${this.index}`);
    return new Promise((res, rej) => {
      // const start = Date.now();
      if (handleBtns) {
        this.btnStart$.disabled = true;
        this.btnRm$.disabled = true;
        this.btnSelect$.disabled = true;
        this.btnReset$.disabled = false;
      }
      const rootStyle = window.getComputedStyle(
        this._rootElement.parentElement as HTMLElement,
      );
      const selfStyle = window.getComputedStyle(this.rootElement);

      const rootWidth =
        Number.parseFloat(rootStyle.width) -
        Number.parseFloat(rootStyle.paddingLeft) -
        Number.parseFloat(rootStyle.paddingRight);
      const selfWidth = Number.parseFloat(selfStyle.width);

      const coordinateInPercents = ((rootWidth - selfWidth) / selfWidth) * 100;

      this._rootElement.classList.add(styles['car-div_rollForward']);

      this.currAnimation = this._rootElement.animate(
        [{ transform: `translateX(${coordinateInPercents}%)` }],
        {
          duration: time * 1000 * 1.5,
          easing: 'ease-in-out',
          fill: 'forwards',
        },
      );

      this.currAnimation.onfinish = () => {
        // console.log(`time from anim start: ${Date.now() - start}`);
        // console.log('stop animation by roll forward finish');
        // this._rootElement.classList.remove(styles['car-div_rollForward']);
        // this.stop();
        res(true);
      };

      this.currAnimation.oncancel = () => {
        // console.log(`curr Anim cancel ${this.index}`);
        this._rootElement.classList.remove(styles['car-div_rollForward']);
        rej();
      };
    });
  }

  public async stop(
    rollover = true,
    broken = false,
    flags?: Record<string, unknown>,
  ) {
    // console.log(`STOP, broken: ${broken}, ${this.index}`);
    // this.rootElement.style.setProperty('transition', '');
    if (rollover) {
      await new Promise(res => {
        if (this.currAnimation?.playState === 'running') {
          // console.log(
          //   this.currAnimation,
          //   `awaiting for animation ${this.index}`,
          // );
          this.currAnimation.addEventListener('finish', () => res(true));
          this.currAnimation.addEventListener('cancel', () => res(true));
        } else res(true);
      });
    }
    this._rootElement.classList.remove(styles['car-div_rollForward']);
    this.currAnimation?.pause();
    if (broken) {
      if (flags) {
        const roadElem = flags.roadElem as HTMLElement;
        const roadIsMoving = roadElem.classList.contains(this.movingClassName);
        console.log(`CHECK IF ROAD IS MOVING ${this.index}`);
        if (roadIsMoving) this.outOfScene();
        this.observer.observe(roadElem, {
          attributes: true,
          attributeOldValue: true,
        });
      }
      console.log(`exp anim ${this.index}`);
      const img = new Image();
      let frameNumber = 0;
      img.classList.add(styles['car-div__explosion']);
      img.src = `${CarComp.CAR_EXPLOSION_IMG_DATA.BASE_URL}${frameNumber}${CarComp.CAR_EXPLOSION_IMG_DATA.EXTENSION}`;
      img.onload = async () => {
        this._rootElement.append(img);
        await new Promise(res =>
          setTimeout(res, CarComp.CAR_EXPLOSION_IMG_DATA.FRAME_TIME_MS),
        );
        frameNumber++;
        if (frameNumber < CarComp.CAR_EXPLOSION_IMG_DATA.FRAMES_COUNT) {
          img.src = `${CarComp.CAR_EXPLOSION_IMG_DATA.BASE_URL}${frameNumber}${CarComp.CAR_EXPLOSION_IMG_DATA.EXTENSION}`;
        } else {
          img.remove();
        }
      };
    }
  }

  public get currCar(): RegCarData {
    return this._currCar;
  }

  public get btnSelect() {
    return this.btnSelect$;
  }

  public get btnRm() {
    return this.btnRm$;
  }

  public get btnStart() {
    return this.btnStart$;
  }

  public get btnReset() {
    return this.btnReset$;
  }

  public get spanCarName() {
    return this._spanCarName;
  }

  static drawCar(canvas: HTMLCanvasElement, hexColor: string) {
    const img = new Image();
    img.src = CarComp.CAR_BODY_IMG_SRC;
    img.onload = () => {
      canvas.height = img.height / 3;
      canvas.width = img.width / 3;
      const ctx = canvas.getContext('2d');

      if (ctx) {
        ctx.shadowColor = hexColor;
        ctx.shadowBlur = 25;
        ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
      }

      CarComp.paintCarBody(canvas, hexColor);
    };
  }

  static paintCarBody(canvas: HTMLCanvasElement, hexColor: string) {
    const imageData = canvas
      .getContext('2d')
      ?.getImageData(0, 0, canvas.width, canvas.height);
    if (imageData) {
      const rgb = hexToRgb(hexColor);
      for (let j = 0; j < imageData.width; j++) {
        for (let i = 0; i < imageData.height; i++) {
          // index: red, green, blue, alpha, red, green, blue, alpha..etc.
          const index = i * 4 * imageData.width + j * 4;
          const red = imageData.data[index] as number;
          const green = imageData.data[index + 1] as number;
          const blue = imageData.data[index + 2] as number;
          // const alpha = imageData.data[index + 3] as number;

          const blueDiff = 2 * blue - red - green;
          const blueDiffThreshold = 10;
          const threshold = 70;
          if (
            red > threshold &&
            green > threshold &&
            blue > threshold &&
            blueDiff < blueDiffThreshold
          ) {
            imageData.data[index] = (red / 255) * rgb.r;
            imageData.data[index + 1] = (green / 255) * rgb.g;
            imageData.data[index + 2] = (blue / 255) * rgb.b;
            // imageData.data[index + 3] = 225; /// !!! TEST !!!
          }
        }
      }
      canvas
        .getContext('2d')
        ?.putImageData(imageData, 0, 0, 0, 0, canvas.width, canvas.height);
    }
  }

  static genPreview(car: UnregCarData): HTMLElement {
    const container = document.createElement('div');
    const canvas = document.createElement('canvas');
    canvas.classList.add(styles['car-div__canvas']);
    container.append(canvas);
    CarComp.drawWheels(container);
    CarComp.drawCar(canvas, car.color);
    return container;
  }
}
