import { RegCarData, UnregCarData } from '../../../definitions/car-data.def';
import { OBJ_PROCESSOR } from '../../../lib/processors/obj-processor';
import { REST_CLIENT_ERROR } from '../rest-clients-errors.def';
import { REST_CLIENT_NAMES } from '../rest-clients-names.def';

class RestClientGarage {
  protected clientName = REST_CLIENT_NAMES.GARAGE;

  private resUrl = '/garage';

  private fullUrl: string;

  constructor(private baseUrl: string) {
    this.fullUrl = baseUrl + this.resUrl;
  }

  getCars(
    page?: number,
    limit?: number,
  ): Promise<{ limit: string; cars: RegCarData[] }> {
    return new Promise((res, rej) => {
      const limitStr = limit || '';
      const url = `${this.fullUrl}?_page=${page}&_limit=${limitStr}`;
      let limitResponse = '';
      fetch(url, {
        method: 'GET',
      })
        .then(response => {
          if (response.status === 200) {
            limitResponse = response.headers.get('X-Total-Count') as string;
            return response.json();
          }
          throw new Error(REST_CLIENT_ERROR.UNEXPECTED_RESULT);
        })
        .then(json => res({ limit: limitResponse, cars: json as RegCarData[] }))
        .catch((error: Error) => {
          if (error.message in REST_CLIENT_ERROR) rej(error);
          if (error.message === 'Failed to fetch')
            rej(new Error(REST_CLIENT_ERROR.CONNECTION_REFUSED));
          else rej(REST_CLIENT_ERROR.UNKNOWN_ERROR);
        });
    });
  }

  getCar(id: number): Promise<RegCarData> {
    return new Promise((res, rej) => {
      const url = `${this.fullUrl}/${id}`;
      fetch(url, {
        method: 'GET',
      })
        .then(response => {
          if (response.status === 200) return response.json();

          if (response.status === 404)
            throw new Error(REST_CLIENT_ERROR.NOT_FOUND);
          throw new Error(REST_CLIENT_ERROR.UNEXPECTED_RESULT);
        })
        .then(json => res(json as RegCarData))
        .catch(error => {
          if (error.message in REST_CLIENT_ERROR) rej(error);
          if (error.message === 'Failed to fetch')
            rej(new Error(REST_CLIENT_ERROR.CONNECTION_REFUSED));
          else rej(REST_CLIENT_ERROR.UNKNOWN_ERROR);
        });
    });
  }

  createCar(unregCar: UnregCarData): Promise<RegCarData> {
    return new Promise((res, rej) => {
      fetch(this.fullUrl, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(unregCar),
      })
        .then(response => {
          if (response.status === 201) return response.json();
          throw new Error(REST_CLIENT_ERROR.UNEXPECTED_RESULT);
        })
        .then(json => res(json as RegCarData))
        .catch(error => {
          if (error.message in REST_CLIENT_ERROR) rej(error);
          if (error.message === 'Failed to fetch')
            rej(new Error(REST_CLIENT_ERROR.CONNECTION_REFUSED));
          else rej(REST_CLIENT_ERROR.UNKNOWN_ERROR);
        });
    });
  }

  deleteCar(id: number): Promise<true> {
    return new Promise((res, rej) => {
      const url = `${this.fullUrl}/${id}`;
      fetch(url, {
        method: 'DELETE',
      })
        .then(response => {
          if (response.status === 200) res(true);
          if (response.status === 404)
            throw new Error(REST_CLIENT_ERROR.NOT_FOUND);
          throw new Error(REST_CLIENT_ERROR.UNEXPECTED_RESULT);
        })
        .catch(error => {
          if (error.message in REST_CLIENT_ERROR) rej(error);
          if (error.message === 'Failed to fetch')
            rej(new Error(REST_CLIENT_ERROR.CONNECTION_REFUSED));
          else rej(REST_CLIENT_ERROR.UNKNOWN_ERROR);
        });
    });
  }

  updateCar(regCar: RegCarData): Promise<RegCarData> {
    return new Promise((res, rej) => {
      const url = `${this.fullUrl}/${regCar.id}`;
      const data: UnregCarData = regCar;
      fetch(url, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data),
      })
        .then(response => {
          if (response.status === 200) return response.json();
          if (response.status === 404)
            throw new Error(REST_CLIENT_ERROR.NOT_FOUND);
          throw new Error(REST_CLIENT_ERROR.UNEXPECTED_RESULT);
        })
        .then(json => res(json))
        .catch(error => {
          if (error.message in REST_CLIENT_ERROR) rej(error);
          if (error.message === 'Failed to fetch')
            rej(new Error(REST_CLIENT_ERROR.CONNECTION_REFUSED));
          else rej(REST_CLIENT_ERROR.UNKNOWN_ERROR);
        });
    });
  }
}

const obj = new RestClientGarage('http://127.0.0.1:3000');

const REST_CLIENT_GARAGE = OBJ_PROCESSOR.deepFreeze(obj);

export { REST_CLIENT_GARAGE };
