import { BaseComp } from '../../base-comp/base-comp';

export interface BasePopupAttrs {
  configurable: boolean;
}

export abstract class PopupStandaloneComp extends BaseComp {
  protected body: HTMLElement;

  protected header: HTMLHeadingElement;

  protected text: HTMLParagraphElement;

  constructor() {
    super('div');

    this.body = document.createElement('div');
    this.header = document.createElement('h3');
    this.text = document.createElement('p');

    this.body.append(this.header, this.text);
    this._rootElement.append(this.body);
  }
}
