import { DBusTopics } from './defs/dbus-topics.def';
import { genUniqId } from '../../generators/generators';
import { OBJ_PROCESSOR } from '../../processors/obj-processor';
import { DBUS_ERRORS } from './defs/dbus-errors.def';
import { DBusRX, DBusRXFunc } from './defs/dbus-rx/dbus-rx.def';
import { DBusTX } from './defs/dbus-tx/dbus-tx.def';
import { DBusTXOptions } from './defs/dbus-tx/dbus-tx-options.def';
import { DBusTXData } from './defs/dbus-tx/dbus-tx-data.def';
import {
  DBusTXPrepModes,
  DBusTXWaitModes,
} from './defs/dbus-tx/dbus-tx-modes.def';
import { DBusTXDataHeaders } from './defs/dbus-tx/dbus-tx-data-headers.def';
import { DBusHub, DBusInterface } from './defs/dbus-interface.def';
import { DBUS_HUB_NAMES } from './defs/dbus-hub-names.def';

export class DBus {
  private network: Map<DBusTopics, Set<DBusRX>> = new Map();

  private idCounter = {
    txs: 0,
    rxs: 0,
  };

  private createdRXs = new Set<DBusRX>();

  private createdTXs = new Set<DBusTX>();

  private attachedHubs = new Map<DBusHub, DBusInterface>();

  private occupiedNames = new Set<string>();

  readonly busId: string;

  constructor() {
    this.busId = genUniqId();
    return Object.freeze(this) as unknown as DBus;
  }

  public attachHub(hub: DBusHub, suffix?: string): void | Promise<void> {
    return this.createInterfaceForHub(hub, undefined, suffix);
  }

  private createInterfaceForHub(
    hub: DBusHub,
    prefix?: string,
    suffix?: string,
  ): void | Promise<void> {
    if (this.attachedHubs.has(hub))
      throw new Error(DBUS_ERRORS.HUB_IS_ALREADY_ATTACHED);

    if (!(hub.hubName in DBUS_HUB_NAMES))
      throw new Error(DBUS_ERRORS.HUB_NAME_NOT_AUTHORIZED);

    let hubName = prefix ? `${prefix}_${hub.hubName}` : hub.hubName;
    if (suffix) hubName += `_${suffix}`;

    if (this.occupiedNames.has(hubName))
      throw new Error(DBUS_ERRORS.HUB_NAME_IS_OCCUPIED);

    const arrowedGetNewTX = (desc?: string) => this.getNewTX(hubName, desc);
    const arrowedGetNewRX = (func: DBusRXFunc, desc?: string) =>
      this.genNewRX(func, desc);
    const arrowedPlugRX = (topic: DBusTopics, rx: DBusRX) =>
      this.plugRX(topic, rx);
    const arrowedUnplugRX = (topic: DBusTopics, rx: DBusRX) =>
      this.unplugRX(topic, rx);
    const decoratedAttachHub = (subHub: DBusHub, subSuffix?: string) =>
      this.createInterfaceForHub(subHub, hubName, subSuffix);

    const dbusInterface = new DBusInterface(
      arrowedGetNewTX,
      arrowedGetNewRX,
      arrowedPlugRX,
      arrowedUnplugRX,
      decoratedAttachHub,
    );

    this.occupiedNames.add(hubName);
    this.attachedHubs.set(hub, dbusInterface);

    OBJ_PROCESSOR.deepFreeze(dbusInterface);

    return hub.connect(dbusInterface);
  }

  private getNewTX(hubName: string, desc?: string): DBusTX {
    const id = genUniqId();
    const regNumber = this.idCounter.txs++;

    const arrowedTransmit = async (opts: DBusTXOptions) => {
      const head = new DBusTXDataHeaders(
        hubName,
        id,
        regNumber,
        opts.waitMode,
        opts.prepMode,
        desc,
      );
      const data = new DBusTXData(head, opts.msg);
      const result = await this.transmit(data);
      return result;
    };

    const tx = new DBusTX(regNumber, id, this.busId, arrowedTransmit);

    const freezedTX = OBJ_PROCESSOR.deepFreeze(tx);

    this.createdTXs.add(freezedTX);

    return freezedTX;
  }

  private genNewRX(func: DBusRXFunc, desc?: string): DBusRX {
    const id = genUniqId();
    const regNumber = this.idCounter.rxs++;
    const rx = new DBusRX(regNumber, id, this.busId, func, desc);
    const freezedRX = OBJ_PROCESSOR.deepFreeze(rx);
    this.createdRXs.add(freezedRX);
    return freezedRX;
  }

  private plugRX(topic: DBusTopics, rx: DBusRX): void {
    if (!this.createdRXs.has(rx)) throw new Error(DBUS_ERRORS.XENO_RX);
    if (!this.network.has(topic)) this.network.set(topic, new Set());
    this.network.get(topic)?.add(rx);
  }

  private unplugRX(topic: DBusTopics, rx: DBusRX): void {
    this.network.get(topic)?.delete(rx);
  }

  private async transmit(data: DBusTXData): Promise<unknown> {
    if (!this.network.has(data.msg.topic)) {
      throw new Error(DBUS_ERRORS.TOPIC_IS_NOT_EXIST);
    }

    const rxs = this.network.get(data.msg.topic);

    if (rxs === undefined || rxs.size === 0)
      throw new Error(DBUS_ERRORS.TOPIC_HAS_NO_RXS);

    let choosedRXs: DBusRX[] = [];

    if (data.msg.prefResponseRxIds) {
      rxs.forEach(rx => {
        if (data.msg.prefResponseRxIds?.includes(rx.id)) choosedRXs.push(rx);
      });
      if (choosedRXs.length === 0)
        throw new Error(DBUS_ERRORS.PREF_RXS_ARE_NOT_EXIST);
    } else choosedRXs = [...rxs];

    let result;
    switch (data.headers.waitMode) {
      case DBusTXWaitModes.all:
        result = await Promise.all(
          choosedRXs.map(rx => this.callRXFunc(data, rx)),
        );
        break;
      case DBusTXWaitModes.allSetled:
        result = await Promise.allSettled(
          choosedRXs.map(rx => this.callRXFunc(data, rx)),
        );
        break;
      case DBusTXWaitModes.manual:
        result = choosedRXs;
        break;
      case DBusTXWaitModes.nowait:
      default:
        choosedRXs.map(rx => this.callRXFunc(data, rx));
        result = true;
        break;
    }
    return result;
  }

  private callRXFunc = (data: DBusTXData, rx: DBusRX) => {
    const handleClone = () => {
      const clonedData = OBJ_PROCESSOR.deepClone(data);
      return rx.func(clonedData);
    };

    const handleFreeze = () => {
      const freezedData = OBJ_PROCESSOR.deepFreeze(data);
      return rx.func(freezedData);
    };

    switch (data.headers.prepMode) {
      case DBusTXPrepModes.clone:
        return handleClone();
      case DBusTXPrepModes.freeze:
        return handleFreeze();
      default:
        throw new Error(DBUS_ERRORS.UNEXPECTED_PREP_MODE);
    }
  };
}
