import { WinnerCarData } from '../../../definitions/car-data.def';
import { OBJ_PROCESSOR } from '../../../lib/processors/obj-processor';
import { REST_CLIENT_ERROR } from '../rest-clients-errors.def';
import { RestClientWinnersSortOptionsList } from './rest-client-winners.def';

class RestClientWinners {
  private resUrl = '/winners';

  private fullUrl: string;

  constructor(private baseUrl: string) {
    this.fullUrl = baseUrl + this.resUrl;
  }

  getWinners(
    page = 0,
    limit?: number,
    sort?: RestClientWinnersSortOptionsList,
    order?: 'ASC' | 'DESC',
  ): Promise<{ limit: string; cars: WinnerCarData[] }> {
    return new Promise((res, rej) => {
      const limitStr = limit || '';
      let url = `${this.fullUrl}?_page=${page}&_limit=${limitStr}`;
      if (sort) url += `&_sort=${sort}`;
      if (order) url += `&_order=${order}`;
      let limitResponse = '';
      fetch(url, {
        method: 'GET',
      })
        .then(response => {
          if (response.status === 200) {
            limitResponse = response.headers.get('X-Total-Count') as string;
            return response.json();
          }
          throw new Error(REST_CLIENT_ERROR.UNEXPECTED_RESULT);
        })
        .then(json =>
          res({ limit: limitResponse, cars: json as WinnerCarData[] }),
        )
        .catch((error: Error) => {
          if (error.message in REST_CLIENT_ERROR) rej(error);
          if (error.message === 'Failed to fetch')
            rej(new Error(REST_CLIENT_ERROR.CONNECTION_REFUSED));
          else rej(REST_CLIENT_ERROR.UNKNOWN_ERROR);
        });
    });
  }

  getWinner(id: number): Promise<WinnerCarData> {
    return new Promise((res, rej) => {
      const url = `${this.fullUrl}/${id}`;
      fetch(url, {
        method: 'GET',
      })
        .then(response => {
          if (response.status === 200) return response.json();

          if (response.status === 404)
            throw new Error(REST_CLIENT_ERROR.NOT_FOUND);
          throw new Error(REST_CLIENT_ERROR.UNEXPECTED_RESULT);
        })
        .then(json => res(json as WinnerCarData))
        .catch(error => {
          if (error.message in REST_CLIENT_ERROR) rej(error);
          if (error.message === 'Failed to fetch')
            rej(new Error(REST_CLIENT_ERROR.CONNECTION_REFUSED));
          else rej(REST_CLIENT_ERROR.UNKNOWN_ERROR);
        });
    });
  }

  createWinner(winnerCarData: WinnerCarData): Promise<WinnerCarData> {
    return new Promise((res, rej) => {
      fetch(this.fullUrl, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(winnerCarData),
      })
        .then(response => {
          if (response.status === 201) return response.json();
          throw new Error(REST_CLIENT_ERROR.UNEXPECTED_RESULT);
        })
        .then(json => res(json as WinnerCarData))
        .catch(error => {
          if (error.message in REST_CLIENT_ERROR) rej(error);
          if (error.message === 'Failed to fetch')
            rej(new Error(REST_CLIENT_ERROR.CONNECTION_REFUSED));
          else rej(REST_CLIENT_ERROR.UNKNOWN_ERROR);
        });
    });
  }

  deleteWinner(id: number): Promise<true> {
    return new Promise((res, rej) => {
      const url = `${this.fullUrl}/${id}`;
      fetch(url, {
        method: 'DELETE',
      })
        .then(response => {
          if (response.status === 200) res(true);
          if (response.status === 404)
            throw new Error(REST_CLIENT_ERROR.NOT_FOUND);
          throw new Error(REST_CLIENT_ERROR.UNEXPECTED_RESULT);
        })
        .catch(error => {
          if (error.message in REST_CLIENT_ERROR) rej(error);
          if (error.message === 'Failed to fetch')
            rej(new Error(REST_CLIENT_ERROR.CONNECTION_REFUSED));
          else rej(REST_CLIENT_ERROR.UNKNOWN_ERROR);
        });
    });
  }

  updateWinner(winnerData: WinnerCarData): Promise<WinnerCarData> {
    return new Promise((res, rej) => {
      const url = `${this.fullUrl}/${winnerData.id}`;
      const data: WinnerCarData = winnerData;
      fetch(url, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data),
      })
        .then(response => {
          if (response.status === 200) return response.json();
          if (response.status === 404)
            throw new Error(REST_CLIENT_ERROR.NOT_FOUND);
          throw new Error(REST_CLIENT_ERROR.UNEXPECTED_RESULT);
        })
        .then(json => res(json as WinnerCarData))
        .catch(error => {
          if (error.message in REST_CLIENT_ERROR) rej(error);
          if (error.message === 'Failed to fetch')
            rej(new Error(REST_CLIENT_ERROR.CONNECTION_REFUSED));
          else rej(REST_CLIENT_ERROR.UNKNOWN_ERROR);
        });
    });
  }
}

const obj = new RestClientWinners('http://127.0.0.1:3000');

const REST_CLIENT_WINNERS = OBJ_PROCESSOR.deepFreeze(obj);

export { REST_CLIENT_WINNERS };
