import { BaseComp } from '../../lib/components/base-comp/base-comp';
import { DBus } from '../../lib/services/dbus/dbus';
import { DBUS_COMMON_RESPONSES } from '../../lib/services/dbus/defs/dbus-common-responses.def';
import {
  DBusHubNamesList,
  DBUS_HUB_NAMES,
} from '../../lib/services/dbus/defs/dbus-hub-names.def';
import {
  DBusHub,
  DBusInterface,
} from '../../lib/services/dbus/defs/dbus-interface.def';
import { DBusTopics } from '../../lib/services/dbus/defs/dbus-topics.def';
import { DBusTXData } from '../../lib/services/dbus/defs/dbus-tx/dbus-tx-data.def';
import { DBusMsgRouteIsChangedMsg } from '../../lib/services/simple-router/dbus-msgs-defs/types/dbus-msg-route-is-chaned-msg';
import styles from './header.scss';
import { NavItem } from './subcomps/nav-item/nav-item';

export class HeaderComponent extends BaseComp implements DBusHub {
  private navContainer: HTMLElement;

  private logo: HTMLElement;

  protected anchors: HTMLAnchorElement[];

  protected buses: DBus[] = [];

  constructor(readonly hubName: DBusHubNamesList = DBUS_HUB_NAMES.HEADER) {
    super('header');
    this.rootElement.classList.add(styles.header);

    // logo
    this.logo = document.createElement('h1');
    this.logo.classList.add(styles.header__logo);
    const firstSpan = document.createElement('span');
    firstSpan.textContent = 'async';
    const secondSpan = document.createElement('span');
    secondSpan.textContent = 'race';
    this.logo.append(firstSpan, secondSpan);
    this.rootElement.append(this.logo);

    // nav
    this.navContainer = document.createElement('nav');
    this.navContainer.classList.add(styles['header-nav']);
    this.rootElement.append(this.navContainer);

    // ul
    const ul = document.createElement('ul');
    ul.classList.add(styles['header-nav__list']);
    this.navContainer.append(ul);

    // ul items
    const classList = {
      li: styles['header-nav__item'],
      a: styles['header-nav__anchor'],
      svg: styles['header-nav__svg'],
      span: styles['header-nav__span'],
    };

    const garageNavItem = new NavItem('garage', '#garage', classList);
    const winnersNavItem = new NavItem('winners', '#winners', classList);

    const navItems = [garageNavItem, winnersNavItem];
    this.anchors = navItems.map(item => item.anchor);

    ul.append(...navItems.map(item => item.rootElement));
  }

  connect(int: DBusInterface) {
    const illuminateRX = int.getNewRX(
      this.illuminateNavItem,
      'illuminate nav items',
    );
    int.plugRX(DBusTopics.ROUTING, illuminateRX);
  }

  private illuminateNavItem = async (data: DBusTXData) => {
    const { msg } = data;
    if (msg instanceof DBusMsgRouteIsChangedMsg) {
      this.anchors.forEach(anchor => {
        if (anchor.getAttribute('href') === msg.newRoute) {
          anchor.classList.add(styles['header-nav__anchor_active']);
        } else {
          anchor.classList.remove(styles['header-nav__anchor_active']);
        }
      });
    }
    return DBUS_COMMON_RESPONSES.VOID;
  };
}
