import { UnregCarData } from '../../../../../definitions/car-data.def';
import { DBusInterface } from '../../../../../lib/services/dbus/defs/dbus-interface.def';
import {
  DBusTXPrepModes,
  DBusTXWaitModes,
} from '../../../../../lib/services/dbus/defs/dbus-tx/dbus-tx-modes.def';
import { DBusTX } from '../../../../../lib/services/dbus/defs/dbus-tx/dbus-tx.def';
import { REST_CLIENT_GARAGE } from '../../../../../services/rest-clients/client-garage/rest-client-garage';
import { DBusMsgCarActionCreatedMsg } from '../../../dbus-msgs/by-topic/car-action/garage-car-created-msg.def';
import {
  CreateUpdateCompControllerProto,
  CreateUpdateCompControllerReqElems,
} from './controller-proto.def';

export class CreateCompController extends CreateUpdateCompControllerProto {
  private txs = new Set<DBusTX>();

  constructor(elements: CreateUpdateCompControllerReqElems) {
    super(elements.inputText, elements.inputColor, elements.btn);

    this.setupBtnCreate();
  }

  private setupBtnCreate() {
    this.btn.addEventListener('click', () => {
      this.btn.disabled = true;
      // TODO: !!! add validity check!
      const unregCar: UnregCarData = {
        name: this.inputText.value,
        color: this.inputColor.value,
      };
      REST_CLIENT_GARAGE.createCar(unregCar)
        .then(car => {
          // TODO: Notify with DBus
          const msg = new DBusMsgCarActionCreatedMsg(car);
          this.txs.forEach(tx => {
            tx.transmit({
              msg,
              prepMode: DBusTXPrepModes.clone,
              waitMode: DBusTXWaitModes.nowait,
            });
          });
        })
        .finally(() => {
          this.btn.disabled = false;
        });
    });
  }

  autoConnect(int: DBusInterface) {
    const tx = int.getNewTX('send created car msgs');
    this.txs.add(tx);
  }
}
