import { UnregCarData, WinnerCarData } from '../../../definitions/car-data.def';
import { BaseComp } from '../../../lib/components/base-comp/base-comp';
import { CarComp } from '../../garage/subcomps/car/car';

export class WinnerRowComp extends BaseComp {
  private indexElem = document.createElement('td');

  private idElem = document.createElement('td');

  private carPreviewBox = document.createElement('td');

  private nameElem = document.createElement('td');

  private winsElem = document.createElement('td');

  private bestTimeElem = document.createElement('td');

  private carPreview: HTMLElement;

  constructor(index: number, winnerData: WinnerCarData, carData: UnregCarData) {
    super('tr');

    this.indexElem.textContent = index.toString();
    this.idElem.textContent = winnerData.id.toString();
    this.carPreview = CarComp.genPreview(carData);
    this.carPreviewBox.append(this.carPreview);
    this.nameElem.textContent = carData.name;
    this.winsElem.textContent = winnerData.wins.toString();
    this.bestTimeElem.textContent = winnerData.time.toString();

    this._rootElement.append(
      this.indexElem,
      this.idElem,
      this.carPreviewBox,
      this.nameElem,
      this.winsElem,
      this.bestTimeElem,
    );
  }

  public getCarPreview() {
    return this.carPreview;
  }
}
