import { RegCarData } from '../../../../../definitions/car-data.def';
import { OBJ_PROCESSOR } from '../../../../../lib/processors/obj-processor';
import { DBusInterface } from '../../../../../lib/services/dbus/defs/dbus-interface.def';
import { DBusPort } from '../../../../../lib/services/dbus/defs/dbus-port.def';
import { DBusRX } from '../../../../../lib/services/dbus/defs/dbus-rx/dbus-rx.def';
import { DBusTopics } from '../../../../../lib/services/dbus/defs/dbus-topics.def';
import { DBusTXData } from '../../../../../lib/services/dbus/defs/dbus-tx/dbus-tx-data.def';
import { REST_CLIENT_GARAGE } from '../../../../../services/rest-clients/client-garage/rest-client-garage';
import { DBusMsgCarActionSelectedMsg } from '../../../dbus-msgs/by-topic/car-action/garage-car-select-msg.def';
import {
  CreateUpdateCompControllerProto,
  CreateUpdateCompControllerReqElems,
} from './controller-proto.def';

export class UpdateCompController
  extends CreateUpdateCompControllerProto
  implements DBusPort
{
  private rxs: Set<DBusRX> = new Set();

  private choosedCar: RegCarData | undefined;

  private notifyCb: ((car: RegCarData) => unknown) | undefined;

  constructor(elements: CreateUpdateCompControllerReqElems) {
    super(elements.inputText, elements.inputColor, elements.btn);

    this.setupBtnUpdate();
  }

  autoConnect(int: DBusInterface) {
    const rx = int.getNewRX(this.chooseCarListener, 'listen car choosing');
    int.plugRX(DBusTopics.CAR_ACTION, rx);
  }

  private chooseCarListener = async (data: DBusTXData) => {
    console.log(data);
    if (data.msg instanceof DBusMsgCarActionSelectedMsg) {
      this.choosedCar = OBJ_PROCESSOR.deepClone(data.msg.car);
      this.notifyCb = data.msg.notifyChanges;
      this.inputColor.value = data.msg.car.color;
      this.inputText.value = data.msg.car.name;
      this.toogleCtlsDisabled(false);
    }
  };

  private setupBtnUpdate() {
    this.toogleCtlsDisabled(true);
    this.btn.addEventListener('click', () => {
      this.toogleCtlsDisabled(true);
      if (!this.choosedCar) return;
      // TODO: !!! add validity check!
      this.choosedCar.color = this.inputColor.value;
      this.choosedCar.name = this.inputText.value;
      REST_CLIENT_GARAGE.updateCar(this.choosedCar)
        .then(car => {
          if (this.notifyCb) this.notifyCb(car);
        })
        .finally(() => {
          this.toogleCtlsDisabled(false);
        });
    });
  }
}
