import { BaseComp } from '../../../../../lib/components/base-comp/base-comp';
import { DBUS_HUB_NAMES } from '../../../../../lib/services/dbus/defs/dbus-hub-names.def';
import {
  DBusHub,
  DBusInterface,
} from '../../../../../lib/services/dbus/defs/dbus-interface.def';
import { CreateCompController } from './controller-create';
import {
  CreateUpdateCompControllerProto,
  CreateUpdateCompControllerReqElems,
} from './controller-proto.def';
import { UpdateCompController } from './controller-update';

import styles from './create-update.scss';
import sharedStyles from '../../../../../styles.scss';

export class CreateUpdateComponent extends BaseComp implements DBusHub {
  readonly hubName = DBUS_HUB_NAMES.CREATE_UPDATE;

  protected textInput = document.createElement('input');

  protected colorInput = document.createElement('input');

  protected btn = document.createElement('button');

  protected controller: CreateUpdateCompControllerProto;

  constructor(type: 'CREATE' | 'UPDATE', btnText: string) {
    super('div');
    this._rootElement.classList.add(styles.root);
    this.textInput.type = 'text';
    this.textInput.classList.add(styles['text-input']);
    this.colorInput.type = 'color';
    this.colorInput.classList.add(styles['color-input']);
    this.btn.textContent = btnText;
    this.btn.classList.add(sharedStyles.btn);
    this._rootElement.append(this.textInput, this.colorInput, this.btn);

    const elements: CreateUpdateCompControllerReqElems = {
      inputText: this.textInput,
      inputColor: this.colorInput,
      btn: this.btn,
    };

    switch (type) {
      case 'UPDATE':
        this.controller = new UpdateCompController(elements);
        this.btn.classList.add(sharedStyles['btn-13']);
        break;
      case 'CREATE':
      default:
        this.controller = new CreateCompController(elements);
        this.btn.classList.add(sharedStyles['btn-13']); // TODO: maybe change?
        break;
    }
  }

  connect(int: DBusInterface) {
    this.controller.autoConnect(int);
  }
}
