import { PopupStandaloneModalComp } from '../popup-standalone-modal';

import styles from './popup-progress.scss';
import sharedStyles from '../../../../../../styles.scss';

export class PopupStandaloneModalProgressComp extends PopupStandaloneModalComp {
  private barContainer = document.createElement('div');

  private bar = document.createElement('div');

  constructor() {
    super();
    this.barContainer.classList.add(styles.container);
    this.bar.classList.add(styles.bar);
    this.barContainer.append(this.bar);
    this.text.classList.add(styles.text, sharedStyles['glitched-text']);
    this.body.classList.add(styles.body);
    this.body.append(this.barContainer);
  }

  setText(text: string): PopupStandaloneModalProgressComp {
    super.setText(text);
    return this;
  }

  reset(): PopupStandaloneModalProgressComp {
    this.bar.style.removeProperty('--progress');
    this.text.textContent = '';
    this.text.dataset.textContent = '';
    return this;
  }

  setProgress(progress: number): PopupStandaloneModalProgressComp {
    this.bar.style.setProperty('--progress', `${progress}%`);
    return this;
  }
}
