import { BaseComp } from '../../../../lib/components/base-comp/base-comp';
import { CarComp } from '../car/car';

import styles from './track.scss';

export class TrackComp extends BaseComp {
  private topBlock = document.createElement('div');

  constructor() {
    super('div');
    this._rootElement.classList.add(styles.track);
    this._rootElement.append();
    this.topBlock.classList.add(styles['track__controls-block']);
  }

  illuminateSelf = (illuminate: boolean) => {
    if (illuminate) this._rootElement.classList.add(styles.track_illuminated);
    else this._rootElement.classList.remove(styles.track_illuminated);
  };

  placeCar(carComp: CarComp) {
    if (this._rootElement.hasChildNodes()) {
      this._rootElement.childNodes.forEach(child => child.remove());
    }
    this.topBlock.append(carComp.spanCarName);
    this._rootElement.append(this.topBlock, carComp.rootElement);
  }
}
