import { DBusHubNamesList, DBUS_HUB_NAMES } from './dbus-hub-names.def';
import { DBusRX, DBusRXFunc } from './dbus-rx/dbus-rx.def';
import { DBusTopics } from './dbus-topics.def';
import { DBusTX } from './dbus-tx/dbus-tx.def';

export interface DBusHub {
  readonly hubName: DBusHubNamesList;
  connect: (dbusInterface: DBusInterface) => void | Promise<void>;
}

export function isDBusHub(object: unknown): object is DBusHub {
  const objAsRecord = object as Record<string, unknown>;
  return (
    'connect' in objAsRecord &&
    'hubName' in objAsRecord &&
    typeof objAsRecord.hubName === 'string' &&
    objAsRecord.hubName in DBUS_HUB_NAMES
  );
}

export class DBusInterface {
  private RXs = new Set<DBusRX>();

  private TXs = new Set<DBusTX>();

  private RXsPlugMap = new Map<DBusTopics, Set<DBusRX>>();

  constructor(
    private readonly arrowedGetNewTX: (desc?: string) => DBusTX,
    private readonly arrowedGetNewRX: (
      func: DBusRXFunc,
      desc?: string,
    ) => DBusRX,
    private readonly arrowedPlugRX: (topic: DBusTopics, rx: DBusRX) => void,
    private readonly arrowedUnplugRX: (topic: DBusTopics, rx: DBusRX) => void,
    public readonly attachSubHub: (
      subHub: DBusHub,
      subSuffix?: string,
    ) => void | Promise<void>,
  ) {}

  getNewRX(func: DBusRXFunc, desc?: string) {
    const rx = this.arrowedGetNewRX(func, desc);
    this.RXs.add(rx);
    return rx;
  }

  getNewTX(desc?: string) {
    const tx = this.arrowedGetNewTX(desc);
    this.TXs.add(tx);
    return tx;
  }

  plugRX(topic: DBusTopics, rx: DBusRX): void {
    if (!this.RXsPlugMap.has(topic)) this.RXsPlugMap.set(topic, new Set());
    this.RXsPlugMap.get(topic)?.add(rx);
    this.arrowedPlugRX(topic, rx);
  }

  unplugRX(topic: DBusTopics, rx: DBusRX): void {
    this.RXsPlugMap.get(topic)?.delete(rx);
    this.arrowedUnplugRX(topic, rx);
  }
}
