import { BaseComp } from '../../../../lib/components/base-comp/base-comp';
import { CreateUpdateComponent } from './create-update/create-update';
import {
  DBusHub,
  DBusInterface,
} from '../../../../lib/services/dbus/defs/dbus-interface.def';
import { DBUS_HUB_NAMES } from '../../../../lib/services/dbus/defs/dbus-hub-names.def';
import { DBusMsgRaceRequestMsg } from '../../dbus-msgs/by-topic/garage/garage-race-request-msg.def';
import {
  DBusTXPrepModes,
  DBusTXWaitModes,
} from '../../../../lib/services/dbus/defs/dbus-tx/dbus-tx-modes.def';
import { DBusMsgGarageResetReqMsg } from '../../dbus-msgs/by-topic/garage/garage-reset-req-msg.def';

import styles from './main-controls.scss';
import sharedStyles from '../../../../styles.scss';
import { REST_CLIENT_GARAGE } from '../../../../services/rest-clients/client-garage/rest-client-garage';
import {
  CAR_MODEL_LIST,
  CAR_MODEL_LIST_KEYS,
} from '../../../../definitions/car-random-data.def';
import {
  genRandomHexColor,
  getRandomArbitraryInt,
  getRandomInt,
} from '../../../../lib/generators/generators';
import {
  UnregCarData,
  WinnerCarData,
} from '../../../../definitions/car-data.def';
import { REST_CLIENT_WINNERS } from '../../../../services/rest-clients/client-winners/rest-client-winners';
import { DBusTXData } from '../../../../lib/services/dbus/defs/dbus-tx/dbus-tx-data.def';
import { DBusMsgGarageRaceMsg } from '../../dbus-msgs/by-topic/garage/garage-race-msg.def';
import { DBUS_COMMON_RESPONSES } from '../../../../lib/services/dbus/defs/dbus-common-responses.def';
import { DBusMsgGarageRaceStatusMsg } from '../../dbus-msgs/by-topic/garage/garage-race-status-msg.def';
import { DBusTopics } from '../../../../lib/services/dbus/defs/dbus-topics.def';
import { DBusTX } from '../../../../lib/services/dbus/defs/dbus-tx/dbus-tx.def';
import { DBusMsgGenerateCarsMsg } from './dbus-msgs/generate-cars-msg.def';
import { PopupStandaloneModalProgressComp } from '../../../../lib/components/popups/standalone/modal/progress/popup-progress';

export class MainControlsComp extends BaseComp implements DBusHub {
  readonly hubName = DBUS_HUB_NAMES.MAIN_CONTROLS;

  private txs = new Set<DBusTX>();

  private createComponent = new CreateUpdateComponent('CREATE', 'create');

  private updateComponent = new CreateUpdateComponent('UPDATE', 'update');

  private btnRace = document.createElement('button');

  private btnReset = document.createElement('button');

  private btnGenerate = document.createElement('button');

  private popup = new PopupStandaloneModalProgressComp();

  constructor() {
    super('div');
    this._rootElement.classList.add(styles['main-controls']);

    const btnsBlock = this.renderBtns();
    this.btnRace.addEventListener('click', this.raceClickListener);
    this.btnReset.addEventListener('click', this.resetClickListener);
    this.btnGenerate.addEventListener('click', this.generateClickListener);

    this.btnReset.disabled = true;
    this.btnRace.disabled = true;

    this._rootElement.append(
      this.createComponent.rootElement,
      this.updateComponent.rootElement,
      btnsBlock,
    );
  }

  private generateClickListener = async () => {
    this.btnGenerate.disabled = true;
    const msg = new DBusMsgGenerateCarsMsg('start');
    const transmissions: Promise<unknown>[] = [];
    this.txs.forEach(tx => {
      const transmission = tx.transmit({
        msg,
        prepMode: DBusTXPrepModes.freeze,
        waitMode: DBusTXWaitModes.allSetled,
      });
      transmissions.push(transmission);
    });
    await Promise.allSettled(transmissions);
    this.popup
      .reset()
      .setText('please wait while the entries are generated')
      .show();
    await this.generate(100, undefined, this.genCb);
    this.popup.reset().hide();
    const resolveMsg = new DBusMsgGenerateCarsMsg('end');
    this.txs.forEach(tx => {
      tx.transmit({
        msg: resolveMsg,
        prepMode: DBusTXPrepModes.freeze,
        waitMode: DBusTXWaitModes.nowait,
      });
    });
    this.btnGenerate.disabled = false;
  };

  private genCb = (progress: number) => {
    this.popup.setProgress(progress);
  };

  private raceClickListener = async () => {
    if (this.txs.size === 0) return;
    this.btnRace.disabled = true;
    this.btnGenerate.disabled = true;
    const msg = new DBusMsgRaceRequestMsg();
    this.txs.forEach(tx => {
      tx.transmit({
        msg,
        prepMode: DBusTXPrepModes.freeze,
        waitMode: DBusTXWaitModes.allSetled,
      });
    });
  };

  private resetClickListener = async () => {
    if (this.txs.size === 0) return;
    const msg = new DBusMsgGarageResetReqMsg();
    this.txs.forEach(tx => {
      tx.transmit({
        msg,
        prepMode: DBusTXPrepModes.freeze,
        waitMode: DBusTXWaitModes.allSetled,
      });
    });
  };

  private renderBtns(): HTMLElement {
    // setip btns
    const span = document.createElement('span');
    span.textContent = 'race';
    this.btnRace.append(span);
    this.btnRace.classList.add(sharedStyles.btn, sharedStyles['btn-15']);
    this.btnReset.textContent = 'reset';
    this.btnReset.classList.add(sharedStyles.btn, sharedStyles['btn-9']);
    this.btnGenerate.textContent = 'generate cards';
    this.btnGenerate.classList.add(sharedStyles.btn, sharedStyles['btn-1']);

    const btnBlock = document.createElement('div');
    btnBlock.classList.add(styles['btn-block']);
    btnBlock.append(this.btnRace, this.btnReset, this.btnGenerate);
    return btnBlock;
  }

  private async generate(
    total: number,
    left?: number,
    cb?: (progress: number) => unknown,
  ): Promise<boolean> {
    let genLeft = left || total;
    const randomName = this.genRandomCarName();
    const color = genRandomHexColor();
    const unregCarData: UnregCarData = {
      name: randomName,
      color,
    };
    const car = await REST_CLIENT_GARAGE.createCar(unregCarData);
    const winner: WinnerCarData = {
      id: car.id,
      wins: getRandomArbitraryInt(1, 3),
      time: Number.parseFloat((getRandomArbitraryInt(40, 50) / 3).toFixed(2)),
    };
    await REST_CLIENT_WINNERS.createWinner(winner);
    genLeft--;

    if (cb) {
      const progress = (1 - genLeft / total) * 100;
      cb(progress);
    }

    if (genLeft > 0) return this.generate(total, genLeft, cb);

    return true;
  }

  private genRandomCarName = (): string => {
    const manufacturerNumber = getRandomInt(CAR_MODEL_LIST_KEYS.length - 1);
    const manufacturerName = CAR_MODEL_LIST_KEYS[
      manufacturerNumber
    ] as keyof typeof CAR_MODEL_LIST;
    const models = CAR_MODEL_LIST[manufacturerName];
    const modelNumber = getRandomInt(models.length - 1);
    const modelName = models[modelNumber] as string;
    return `${manufacturerName} ${modelName}`;
  };

  private rxFuncForRaceMsgs = async (data: DBusTXData) => {
    if (data.msg instanceof DBusMsgGarageRaceStatusMsg) {
      const { status } = data.msg;
      switch (status) {
        case 'READY_TO_GO':
          this.btnRace.disabled = false;
          this.btnReset.disabled = true;
          this.btnGenerate.disabled = false;
          // this.btnGenerate.disabled = true;
          break;
        case 'IN_PROCESS':
          this.btnRace.disabled = true;
          this.btnReset.disabled = false;
          this.btnGenerate.disabled = true;
          break;
        case 'NEED_RESET':
          this.btnRace.disabled = true;
          this.btnReset.disabled = false;
          this.btnGenerate.disabled = false;
          break;
        default:
          break;
      }
    }
    return DBUS_COMMON_RESPONSES.VOID;
  };

  connect(int: DBusInterface) {
    int.attachSubHub(this.createComponent, 'cu_create');
    int.attachSubHub(this.updateComponent, 'cu_delete');

    const rx = int.getNewRX(
      this.rxFuncForRaceMsgs,
      'toggle btns in main-ctls-block',
    );
    int.plugRX(DBusTopics.GARAGE, rx);

    const tx = int.getNewTX('start race | broadcast generate');
    this.txs.add(tx);
  }
}
